import { Component, OnInit } from '@angular/core';
import {MatSnackBar} from '@angular/material/snack-bar';
import { CommonService } from 'src/app/common.service';

@Component({
  selector: 'app-courierupdateshipment',
  templateUrl: './courierupdateshipment.component.html',
  styleUrls: ['./courierupdateshipment.component.css']
})
export class CourierupdateshipmentComponent implements OnInit {
  trackingId : number = null;
  status : string = 'in-transit';
  constructor(
    public commonService : CommonService,
    private _snackBar: MatSnackBar
  ) { }

  ngOnInit() {
  }

  openSnackBar(message: string, action: string) {
    this._snackBar.open(message, action);
  }

  updateStatus() {
    if(this.trackingId > 0 && this.commonService.getShippments().length <= this.trackingId){
      let index = this.trackingId-1;
      this.commonService.shipments[index].status = this.status;
      this.openSnackBar('Status Updated','close');
    } else {
      this.openSnackBar('Invalid Tracking Id','close');
    }
  }
}
