
export class Shipment {

    shipperFirstName: string;
    shipperLastName: string;
    shipperCompany: string;
    shipperAddr1: string;
    shipperAddr2: string;
    shipperCity: string;
    shipperRegion: string;
    shipperPostalCode: string;
    shipperCountry: string;
    shipperPhoneNumber: string;
    shipperEmail: string;

    recipientFirstName: string;
    recipientLastName: string;
    recipientCompany: string;
    recipientAddr1: string;
    recipientAddr2: string;
    recipientCity: string;
    recipientRegion: string;
    recipientPostalCode: string;
    recipientCountry: string;
    recipientPhoneNumber: string;
    recipientEmail: string;

    packageWidth: number;
    packageLength: number;
    packageHeight: number;
    packageWeight: number;
    trackingNumber?: number;
    status?: string;

    constructor(){
        this.shipperFirstName = "";
        this.shipperLastName = "";
        this.shipperCompany= "";
        this.shipperAddr1= "";
        this.shipperAddr2= "";
        this.shipperCity= "";
        this.shipperRegion= "";
        this.shipperPostalCode= "";
        this.shipperCountry= "";
        this.shipperPhoneNumber= "";
        this.shipperEmail= "";
    
        this.recipientFirstName= "";
        this.recipientLastName= "";
        this.recipientCompany= "";
        this.recipientAddr1= "";
        this.recipientAddr2= "";
        this.recipientCity= "";
        this.recipientRegion= "";
        this.recipientPostalCode= "";
        this.recipientCountry= "";
        this.recipientPhoneNumber= "";
        this.recipientEmail= "";
    
        this.packageWidth = null;
        this.packageLength = null;
        this.packageHeight = null;
        this.packageWeight = null;
    }
}