import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/shippermodule/home/home.component';
import { LandingComponent } from './components/landing/landing.component';
import { UsertrackingComponent } from './components/usertracking/usertracking.component';
import { CourierhomeComponent } from './components/couriermodule/courierhome/courierhome.component';
import { CreateNewShipmentComponent } from './components/create-new-shipment/create-new-shipment.component';
import { EditshipmentComponent } from './components/editshipment/editshipment.component';
import { ViewshipmentComponent } from './components/couriermodule/viewshipment/viewshipment.component';
import { CourierupdateshipmentComponent } from './components/couriermodule/courierupdateshipment/courierupdateshipment.component';



const routes: Routes = [
  {
    path: '',
    component: LandingComponent,
    data: {
      breadcrumb: 'Home'
    },
    children: [
      {
        path: 'shippingtracking',
        component: UsertrackingComponent,
        data: {
          breadcrumb: 'Tracking'
        },
      }
  ]
},
{
    path: 'courier',
    component : CourierhomeComponent,
    data: {
      breadcrumb: 'Courier Module'
    },
  children: [
    {
      path: 'viewshipment',
      component: ViewshipmentComponent,
      data: {
        breadcrumb: 'View Shipment'
      },
      
    },
    {
      path: 'updateshipment',
      component: CourierupdateshipmentComponent,
      data: {
        breadcrumb: 'Update Shipment'
      },
    }]
},
{
  path: 'shipper',
  component : HomeComponent,
  data: {
    breadcrumb: 'Shipper'
  },
  children: [
    {
      path: 'newshipment',
      component: CreateNewShipmentComponent,
      data: {
        breadcrumb: 'Create Shipment'
      },
    },
    {
      path: 'editshipment',
      component: EditshipmentComponent,
      data: {
        breadcrumb: 'Edit Shipment'
      },
      
    },
    {
      path: 'shippingtracking',
      component: UsertrackingComponent,
      data: {
        breadcrumb: 'Tracking'
      },
    },
  ]
}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
